import React, { useEffect, useState } from "react";
import {
  Box,
  Heading,
  Table,
  TableBody,
  TableCell,
  TableHeader,
  TableRow,
} from "grommet";
import ScaleLoader from "react-spinners/ScaleLoader";

const getBalance = (transactions) => {
  let balance = parseFloat(transactions[0]["amount"]);
  for (let i = 1; i < transactions.length; i++) {
    balance += parseFloat(transactions[i]["amount"]);
  }
  return balance;
};

const getDisplayAmount = (amount) => {
  if (amount.indexOf("-")===0){
    return `-£${amount.substring(1,amount.length)}`;
  } else {
    return `£${amount}`;
  }
}

function Transactions(props) {
  const apiUrl = `https://spreadsheets.google.com/feeds/list/1WYnauh6xhyMscmutXPTeDOUdKVbjYaBVwEt1SbImPeg/${props.user}/public/full?alt=json`;
  const [appState, setAppState] = useState({
    loading: true,
    transactions: [],
    balance: "0",
  });

  useEffect(() => {
    setAppState({ loading: true });
    fetch(apiUrl)
      .then((res) => res.json())
      .then((response) => {
        const transactions = response.feed.entry.map((entry, index) => {
          return {
            id: entry.id.$t.substring(
              entry.id.$t.lastIndexOf("/") + 1,
              entry.id.$t.length
            ),
            key: index,
            date: entry.gsx$date.$t,
            amount: entry.gsx$amount.$t,
            displayAmount: getDisplayAmount(entry.gsx$amount.$t)
          };
        });
        setAppState({
          loading: false,
          balance: getBalance(transactions),
          transactions: transactions.reverse(),
        });
      });
  }, [setAppState, apiUrl]);

  return (
    <Box pad="none" overflow="auto">
      <Heading level="2" margin="small">
        Balance: {!appState.loading ? `£${appState.balance}` : "..."}
      </Heading>
      <Heading level="3" margin="small">
        Transactions
      </Heading>
      <Box direction="row" justify="center">
        <ScaleLoader
          height={50}
          width={4}
          color={"#228BE6"}
          loading={appState.loading}
        />
      </Box>
      {appState.transactions &&
      <Table>
        <TableHeader>
          <TableRow>
            <TableCell scope="col" border="bottom">
              <strong>Date</strong>
            </TableCell>
            <TableCell scope="col" border="bottom">
              <strong>Amount</strong>
            </TableCell>
          </TableRow>
        </TableHeader>
        <TableBody>
          {appState.transactions &&
            appState.transactions.map((transaction, index) => (
              <TableRow key={index}>
                <TableCell scope="row">{transaction.date}</TableCell>
                <TableCell>{transaction.displayAmount}</TableCell>
              </TableRow>
            ))}
        </TableBody>
      </Table>}
    </Box>
  );
}

export default Transactions;
